import React from 'react';

import {
  Card,
  CardContent,
  ListItem,
  ListItemIcon,
  ListItemText,
  makeStyles,
} from '@material-ui/core';
import { Check, CheckBox } from '@material-ui/icons';
import clsx from 'clsx';
import { useDispatch, useSelector } from 'react-redux';
import { useFirestore, useFirestoreConnect } from 'react-redux-firebase';
import { AppState } from 'src/state';
import { applyPreference, DEFAULT_BACKGROUND } from 'src/state/settings';

import {
  User,
  COLLECTION_NAME_MAP,
  updateUserPreference,
} from '../../../lib/api';
import { CommonComponentProps } from '../../components';

const useStyles = makeStyles((theme) => ({
  root: {},
  colorItemContainer: {
    display: 'grid',
    gridTemplateRows: 'repeat(3, auto)',
    gridGap: '1em',
  },
  colorItem: {
    width: '90%',
    height: '3rem',
    borderRadius: 10,
    '& .text': {},
  },
}));

interface ColorItemProps {
  color: string;
  label: string;
  isSelected?: boolean;
  onSelect?: () => void;
}

const BackgroundOption: React.FC<ColorItemProps> = ({
  isSelected = false,
  ...props
}: ColorItemProps) => {
  const classes = useStyles();
  const [itemSelected, setItemSelection] = React.useState(isSelected);
  React.useEffect(() => {
    setItemSelection(isSelected);
  }, [isSelected]);
  const onItemSelection = () => {
    setItemSelection(!itemSelected);
    props.onSelect && props.onSelect();
  };
  return (
    <div
      className={classes.colorItem}
      style={{ backgroundColor: props.color }}
      onClick={onItemSelection}>
      <ListItem>
        <ListItemIcon>
          <CheckBox />
        </ListItemIcon>
        <ListItemText className='text' primary={props.label} />
      </ListItem>
    </div>
  );
};

export interface BackgroundPickerProps extends Partial<CommonComponentProps> {
  user: User;
}

export const BackgroundPicker: React.FC<BackgroundPickerProps> = (
  props: BackgroundPickerProps
) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  useFirestoreConnect([COLLECTION_NAME_MAP.DISPLAY]);
  const firestore = useFirestore();
  const DISPLAY_CONFIG = useSelector(
    (st: AppState) => st.firestore.data[COLLECTION_NAME_MAP.DISPLAY]
  );
  const USER_BG_COLOR = props.user.preference.display.background;
  const getColors = (): string[] => {
    const BG_COLORS = [
      ...(DISPLAY_CONFIG && DISPLAY_CONFIG.CONFIG
        ? DISPLAY_CONFIG.CONFIG.colors
        : DEFAULT_BACKGROUND),
    ];
    if (USER_BG_COLOR && BG_COLORS.indexOf(USER_BG_COLOR) === -1) {
      BG_COLORS.splice(0, 1, USER_BG_COLOR);
    }
    return BG_COLORS;
  };
  const updateUserDisplayColor = (color: string) => {
    const userPreference = {
      ...props.user.preference,
      display: {
        ...props.user.preference.display,
        color,
      },
    };
    dispatch(applyPreference(userPreference));
    updateUserPreference(firestore, {
      ...props.user,
      preference: userPreference,
    });
  };
  return (
    <Card variant={'outlined'} className={clsx(props.className, classes.root)}>
      <CardContent className={classes.colorItemContainer}>
        {getColors().map((c: string, i: number) => {
          // TODO:
          return (
            <BackgroundOption
              key={i}
              color={c}
              label={'hello'}
              isSelected={USER_BG_COLOR === c}
              onSelect={() => updateUserDisplayColor(c)}
            />
          );
        })}
      </CardContent>
    </Card>
  );
};

export default BackgroundPicker;
